.level2 {
  height: 100%;
  background: #fff;
}
.level2 ._desc {
  padding: 0 8px;
  box-sizing: border-box;
  color: #333;
}
.level2 ._desc h2 {
  font-size: 22px;
  line-height: 36px;
  margin-bottom: 10px;
  text-align: center;
}
.level2 ._desc p {
  text-indent: 2em;
  font-size: 13px;
  line-height: 22px;
  margin-bottom: 4px;
}
.level2 main {
  margin-top: 14px;
}
.level2 main .am-accordion .am-accordion-item .am-accordion-header {
  background: #eee;
}

